.search-results {
  padding: 3rem 0;
  display: flex;
  flex-direction: column;
  // flex-direction: row;
  // flex: 50%;
}

.results {
  list-style: none;
  margin-bottom: 2rem;
}

@media screen and (max-width: 784px) {
  .results {
    width: 100%;
    margin: 2rem 0;
  }
}

.pagination {
  margin-top: auto;
  padding: 0 3.5rem;

  &::after {
    content: '';
    display: table;
    clear: both;
  }

  &__btn {
    &--prev {
      float: left;
    }
    &--next {
      float: right;
    }
  }
}

.copyright {
  color: var(--color-text-2);
  font-size: 1.2rem;
  padding: 0 3.5rem;
  margin-top: 4rem;

  .twitter-link:link,
  .twitter-link:visited {
    color: var(--color-text-2);
  }
}
